<template>
  <header>
    <div>
      <h1>EMSA Unit Workload</h1>
      <div class="right">
        <h5 v-if="this.$route.name !== 'Login'">Updated At: {{ updatedAt }}</h5>
        <Navbar />
      </div>
    </div>
    <div class="load-bar-container" v-if="this.$route.name !== 'Login'">
      <div id="load-bar"></div>
    </div>
  </header>
</template>

<script>
import Navbar from "./Navbar";
export default {
  name: "Header",
  components: {
    Navbar
  },
  data() {
    return {
      updatedAt: ""
    };
  },
  methods: {
    watchUpdatedAt() {
      this.$store.watch(() => {
        this.updatedAt = this.$store.getters["GET_LAST_UPDATED_TIME"];
      });
    }
  },
  created() {
    this.watchUpdatedAt();
  }
};
</script>

<style lang="scss" scoped>
header {
  background: var(--primary);
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  width: inherit;
  border-bottom: solid 1px white;

  & > div:nth-child(1) {
    display: flex;
    justify-content: space-between;
  }

  h1 {
    font-size: 0.9rem;
    padding: 1em;
  }

  .right {
    display: flex;

    h5 {
      padding: 1em;
    }
  }

  .load-bar-container {
    height: 5px;
    width: 100%;

    #load-bar {
      background: white;
      height: 100%;
    }
  }
}
</style>
